* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #0B2434;
    padding: 20px;
    font-family: 'Karla', sans-serif;

}

main {
    background-color: #F5F5F5;
    height: 400px;
    max-width: 800px;
    border-radius: 5px;
    padding: 20px;
    display: flex;

    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.number-of-roll {
    margin-right: auto;
    border-bottom: 4px dotted blue;
    color: deeppink;
    font-weight: 700px;
    font-size: 25px;
}
.best-score {
    margin-left: auto;
    margin-top: -33px;
    font-size: 20px;
    color:darkorchid;
}
.best-score .number-of-roll {
    color: orangered;
}

.title {
    font-size: 40px;
    margin: 0;
}

.dice-container {
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    gap: 20px;
    /* margin-bottom: 40px; */
}

.die-face {
    height: 50px;
    width: 50px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}


.die-num {
    font-size: 2rem;
}

.roll-btn {
    padding: 15px 45px;
    margin-top: 50px;
    border: none;
    border-radius: 6px;
    background: #5035FF;
    color: white;
    font-size: 1.2rem;   
    font-weight: 700;
}

.roll-btn:focus {
    outline: none;
}
.roll-btn:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

